[data-list] {
  .sort[data-sort] {
    white-space: nowrap;
    cursor: pointer;
    &::after {
      content: url("data:image/svg+xml;base64,");
      margin-left: .25rem;
    }
  }
  .sort[data-sort].asc {
    &::after {
      content: url("data:image/svg+xml;base64,") !important;
      margin-top: -1px !important;
    }
  }
  .sort[data-sort].desc {
    &::after {
      content: url("data:image/svg+xml;base64,") !important;
      margin-top: -1px !important;
    }
  }
}